.register {
    /* width: 400px; */
    background: rgb(47, 170, 183);
    border: 1px solid #dddfe2;
    box-shadow: 0 2px 4px rgb(0 0 0 / 10%), 0 8px 16px rgb(0 0 0 / 10%);
    border-radius: 8px;
    padding: 1rem;
    align-items: center;
    text-align: center;
}

.input {
    border-radius: 8px;
    border: 2px solid #dddfe2;
    outline: none;
    color: #1d2129;
    margin: 0.5rem 0;
    padding: 0.5rem 0.75rem;
    width: 92%;
    font-size: 1rem;
}

.login {
    background: #50da41;
    border: 1px solid #1877f2;
    color: #fff;
    font-size: 1.25rem;
    padding: 0.5rem;
    margin: 0.5rem 0;
    border-radius: 8px;
    outline: none;
    cursor: pointer;
}

.login-btn, .register-btn{
    
    color: black;
    font-size: 1.25rem;
    padding: 0.5rem;
    margin: 0.5rem 0;
    outline: none;
    cursor: pointer;
    border: none;
    background: none;
}

.or{
    background: none;
    border: none;
}